*{margin:0;padding:0;box-sizing:border-box}html,body{width:100%;height:100%;margin:0;padding:0;overflow:hidden}body{background-image:url(./assets/picture/BG/背景-活動中心.png);background-size:cover;background-repeat:no-repeat;background-position:center;display:flex;justify-content:center;align-items:center;flex-direction:column}.container,.centered-container{display:flex;justify-content:center;align-items:center;flex-direction:column;text-align:center;width:100%;height:100%}.dialog{display:flex;justify-content:center;align-items:center;flex-direction:column;width:100%;height:100%;position:relative}.centered-image{max-width:80%;max-height:80%;margin:auto}.foundButton{height:3rem;padding:15px 30px;border-radius:10px;background:#d5f365;border:none;font-family:inherit;color:#2e3730;font-weight:bold;font-size:1.5rem;cursor:pointer;position:absolute;bottom:20px;left:50%;transform:translateX(-50%);display:flex;justify-content:center;align-items:center}.foundButton:hover{background-color:#c4e056}@media screen and (max-width: 768px){html{transform:rotate(90deg)}body{width:100%;height:100%;transform-origin:center center}.foundButton{padding:10px 20px;font-size:1rem;bottom:10px}.centered-image{max-width:80%;max-height:80%}.custom-modal{width:90vw;padding:10px}.custom-modal-content{font-size:1.25rem}.custom-modal-button{padding:8px 16px;font-size:1rem}.assemble-button{padding:8px 16px;font-size:1rem}}