.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding: 20px;
    font-family: Arial, sans-serif;
    height: 100vh;
    /* 讓容器佔滿整個視窗高度 */
}

.mapContainer {
    width: 80%;
    max-width: 400px;
    margin-bottom: 20px;
}

.mapImage {
    width: 100%;
    height: auto;
}

.buttonsContainer {
    display: flex;
    flex-direction: row;
    /* 水平排列 */
    justify-content: center;
    /* 水平居中 */
    margin-bottom: 20px;
}

.button {
    padding: 10px 20px;
    font-size: 16px;
    background-color: #4caf50;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin: 5px;
    /* 調整按鈕之間的間距 */
}

.button.audioPlayed {
    background-color: #45a049;
}

.resultContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.input {
    padding: 10px;
    font-size: 16px;
    border-radius: 5px;
    border: 1px solid #ccc;
    width: 200px;
    margin-bottom: 10px;
}

.image {
    width: 80%;
    max-width: 600px;
    margin: 20px 0;
}